<template>
    <transition name="fade"
        mode="out-in">
        <div v-if="loading"
            class="app-preloader"
            :class="classes">
            <div class="app-preloader__icon">
                <div class="preloader">
                </div>
            </div>
        </div>
        <template v-else-if="this.$slots.default">
            <!-- @slot Use this slot default for show content after loading -->
            <slot></slot>
        </template>
    </transition>
</template>

<script>
export default {
    name: "AppPreloader",
    props: {
        /**
         * Loading state
         */
        loading: {
            type: Boolean,
            default: true
        },
        /**
         * Set styles for using in component
         */
        inComponent: Boolean
    },
    mounted() {
        if (this.inComponent) {
            this.$parent.$el.style.position = "relative";
        }
    },
    computed: {
        /**
         * Return classes for using in component
         */
        classes() {
            return {
                "app-preloader--in-component": this.inComponent
            };
        }
    }
};
</script>

<style lang="scss">
@include b(preloader) {
    align-items: center;
    background-color: $color-white;
    border-radius: inherit;
    color: $color-black;
    display: flex;
    height: 100%;
    justify-content: center;
    left: 0;
    overflow: hidden;
    pointer-events: none;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
    @include e(icon) {
        .preloader {
            animation: loading 0.75s infinite linear;
            border: 0.125rem solid rgba($color-black, 0.2);
            border-bottom-color: rgba($color-black, 0.2);
            border-left-color: $color-black;
            border-radius: 50%;
            border-right-color: rgba($color-black, 0.2);
            border-top-color: rgba($color-black, 0.2);
            display: inline-block;
            font-size: inherit;
            height: 1.5rem;
            overflow: hidden;
            pointer-events: none;
            position: relative;
            width: 1.5rem;
        }
    }
    @include m(in-component) {
        background-color: rgba($color-white, 0.75);
        color: $color-white;
        position: absolute;
    }
}
</style>