<template>
    <div class="app-form-item form-group">
        <label class="app-form-item__label">{{label}}</label>
        <input v-if="type == 'text'"
            :class="fieldClasses"
            v-model="value"
            v-on="{ [validation.trigger]: setError }"
            :placeholder="label"
            :required="validation.required">
        <textarea v-if="type == 'textarea'"
            :class="fieldClasses"
            v-model="value"
            v-on="{ [validation.trigger]: setError }"
            :placeholder="label"
            :required="validation.required"></textarea>
        <select v-if="type == 'dropdown'"
            :class="fieldClasses"
            v-model="value"
            v-on="{ [validation.trigger]: setError }"
            :required="validation.required">
            <option disabled
                selected
                :value="emptyValue">{{label}}</option>
            <template v-for="(item, index) in data">
                <option :key="index"
                    :value="item">{{item}}</option>
            </template>
        </select>
        <small v-if="showError"
            class="app-form-item__error form-text">
            {{validation.message}}
        </small>
    </div>
</template>

<script>
export default {
    name: "AppFormItem",
    data: function() {
        return {
            value: "",
            showError: false,
            validation: {
                message: "",
                required: false,
                trigger: "",
                validator: ""
            }
        };
    },
    props: {
        /**
         * Name for v-model and rules
         */
        prop: [String, Number],
        /**
         * List for dropdown item
         */
        data: [Array],
        /**
         * Item label
         */
        label: {
            type: String,
            default: ""
        },
        /**
         * Type item
         * `text, textarea, dropdown`
         */
        type: {
            type: String,
            default: "text"
        }
    },
    watch: {
        /**
         * Watch model value and set for parent form
         */
        value() {
            this.$parent.value[this.prop] = this.value;
        }
    },
    mounted() {
        if (this.$parent.value) {
            this.value = this.$parent.value[this.prop];
        }

        if (this.$parent.rules) {
            let { 0: validation } = this.$parent.rules[this.prop];
            this.validation = validation;
        }
    },
    methods: {
        /**
         * Show error on target validation
         */
        setError(event) {
            this.showError = this.validation.validator
                ? this.validation.validator(event.target.value)
                : false;
        }
    },
    computed: {
        /**
         * Classes for item
         *
         * @returns {Array} Classes
         */
        fieldClasses() {
            return ["app-form-item__field form-control"];
        },
        emptyValue() {
            return [0, "", null, undefined].find(item => item === this.value);
        }
    }
};
</script>


<style lang="scss">
@include b(form-item) {
    display: inline-flex;
    flex-wrap: wrap;

    @include e(label) {
        font-size: 0.85rem;
        margin-bottom: 0.25rem;
    }

    @include e(error) {
        font-size: 0.75rem;
        color: $color-danger;
    }
}
</style>