<template>
    <div class="app-home">
        <h2 class="w-100 text-center mb-5">Simple form</h2>

        <app-form v-model="form"
            :rules="rules">
            <app-form-item type="text"
                label="Name"
                prop="name">
            </app-form-item>
            <app-form-item type="dropdown"
                label="Count"
                prop="count"
                :data="countList">
            </app-form-item>
            <app-form-item type="textarea"
                label="Comment"
                prop="comment">
            </app-form-item>
        </app-form>

        <code class="w-100 text-center mt-5">
            form: {{form}}
        </code>
    </div>
</template>

<script>
export default {
    name: "Home",
    data() {
        return {
            form: {
                name: "",
                count: null,
                comment: ""
            },
            countList: [1, 2, 3, 4, 5],
            rules: {
                name: [
                    {
                        required: true,
                        validator: this.errorName,
                        message: "Please enter 'John Doe'",
                        trigger: "blur"
                    }
                ],
                count: [
                    {
                        required: true,
                        validator: this.errorCount,
                        message: "Please select 3 or 4",
                        trigger: "change"
                    }
                ],
                comment: [
                    {
                        required: false,
                        validator: this.errorCcomment,
                        message: "Please enter 'Hello'",
                        trigger: "input"
                    }
                ]
            }
        };
    },
    methods: {
        errorName(name) {
            if (name == "John Doe") {
                return false;
            } else {
                return true;
            }
        },
        errorCount(num) {
            if (num == 3 || num == 4) {
                return false;
            } else {
                return true;
            }
        },
        errorCcomment(comment) {
            if (comment == "Hello") {
                return false;
            } else {
                return true;
            }
        }
    }
};
</script>

